<template>
  <v-toolbar dense floating style="z-index: 5;">
    <v-text-field hide-details prepend-icon="mdi-magnify" single-line placeholder="  " @keyup.enter="sendData"
      v-model="sigungu"></v-text-field>
    <v-row justify="center">
      <v-dialog v-model="dialog" scrollable max-width="600px">
        <template v-slot:activator="{ on, attrs }">
          <!--    -->
          <v-btn icon color="primary" dark v-bind="attrs" v-on="on" style="margin-bottom: 12px;">
            <v-icon>mdi-playlist-plus</v-icon>
          </v-btn>
        </template>
        <v-card>
          <v-card-title> </v-card-title>
          <v-divider></v-divider>
          <!--  -->
          <v-card-text>
            <template>
              <v-container fluid>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-select :items="['', '', '']" label="" required v-model="houseType"></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-select :items="['', '', '', '']" label="" required v-model="saleType"></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-select :items="['', '1 ', '5 ', '10 ', '15 ']" label="" required
                      v-model="constructionYear">
                    </v-select>
                  </v-col>
                  <v-col class="12">
                    <v-range-slider :tick-labels="houseSize" v-model="sizeLevel" min="0" max="6" ticks="always"
                      tick-size="4" ref="sizeLevel">
                      <template v-slot:thumb-label="props">
                      </template>
                    </v-range-slider>
                  </v-col>
                  <v-col cols="12">
                    <v-autocomplete :items="['', '', '', '1', '2', '3', '4', '5', '6']" label=""
                      multiple v-model="tags"></v-autocomplete>
                  </v-col>
                </v-row>
                <p>   {{ tags }}</p>
              </v-container>
            </template>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="save">
              
            </v-btn>
            <v-btn color="blue darken-1" text @click="dialog = false">
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-toolbar>
</template>

<script>
  import axios from 'axios';

  export default {
    props: ['sigungu'],
    data() {
      return {
        dialogm1: '',
        dialog: false,
        tags: '',
        houseSize: [
          '~10',
          '10',
          '20',
          '30',
          '40',
          '50',
          '60~',
        ],
        houseType: '',
        saleType: '',
        constructionYear: '',
        sizeLevel: [0, 6]
      }
    },
    methods: {
      save() {
        this.dialog = false;

        let year = new Date().getFullYear();
        switch (this.constructionYear) {
          case '1 ':
            year -= 1;
            break;
          case '5 ':
            year -= 5;
            break;
          case '10 ':
            year -= 10;
            break;
          case '15 ':
            year -= 15;
            break;
          default:
            year -= 1000;
            break;
        }

        let minSize = this.$refs.sizeLevel.value[0];
        let maxSize = this.$refs.sizeLevel.value[1];

        switch (minSize) {
          case 0:
            minSize = 0;
            break;
          case 1:
            minSize = 33;
            break;
          case 2:
            minSize = 66;
            break;
          case 3:
            minSize = 99;
            break;
          case 4:
            minSize = 132;
            break;
          case 5:
            minSize = 165;
            break;
          case 6:
            minSize = 10000;
            break;
        }

        switch (maxSize) {
          case 0:
            minSize = 0;
            break;
          case 1:
            maxSize = 33;
            break;
          case 2:
            maxSize = 66;
            break;
          case 3:
            maxSize = 99;
            break;
          case 4:
            maxSize = 132;
            break;
          case 5:
            maxSize = 165;
            break;
          case 6:
            maxSize = 10000;
            break;
        }

        console.log(minSize, maxSize);

        let tagsToString = '';
        for (let i = 0; i < this.tags.length; i++) {
          tagsToString += this.tags[i] + '/';
        }
        console.log(tagsToString);

      },
      sendData() {
        this.$emit("get-property-list", this.sigungu);
      },
    }
  }
</script>