<template>
<form id="updateProperty">
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn class="mx-2" fab depressed dark x-small color="#B3E3C3" v-bind="attrs" v-on="on">
          <v-icon dark>
            mdi-pencil
          </v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5">  </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-select :items="['', ' ']" label=" " required v-model="state" ref="productState">
                </v-select>
              </v-col>
              <v-col cols="12" sm="6">
                <v-file-input :label="houseDetail[0].mainImg" ref="mainImg" id="mainImage"></v-file-input>
              </v-col>
              <v-col cols="12" sm="6">
                <v-file-input label=' ' multiple small-chips truncate-length="15"></v-file-input>
              </v-col>
              <v-col cols="12">
                <v-text-field label="" required v-model="houseDetail[0].houseName" ref="houseName"></v-text-field>
              </v-col>
              <!--  ,  -->
              <v-col cols="12" sm="6">
                <v-select :items="['', '', '']" label=" " required v-model="houseDetail[0].saleType"
                  ref="saleType"></v-select>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="()" hint="  / ." required v-model="houseDetail[0].price"
                  ref="price"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="" hint="  " required v-model="houseDetail[0].sigungu" ref="sigungu">
                </v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label=" " hint=" 403" required :value="houseDetail[0].streetAddress"
                  ref="streetAddress"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-select :items="['', '', '']" label="" required v-model="houseDetail[0].houseType"
                  ref="houseType"></v-select>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="" required v-model="houseDetail[0].floor" ref="floor"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label=" m" required v-model="houseDetail[0].areaExclusive" ref="areaExclusive">
                </v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-select :items="['', '', '', '']" label="" required v-model="houseDetail[0].houseFace"
                  ref="houseFace"></v-select>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="" required v-model="houseDetail[0].constructionYear" ref="constructionYear">
                </v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-select :items="['', '']" label=" " required v-model="parking" ref="parking"></v-select>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label=" " :value="houseDetail[0].roomCnt" ref="roomCnt"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label=" " required v-model="houseDetail[0].bathCnt" ref="bathCnt"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-chip-group multiple active-class="green--text" v-model="selectedTags">
                  <v-chip v-for="tag in tags" :key="tag" filter outlined>
                    {{ tag }}
                  </v-chip>
                </v-chip-group>
              </v-col>
              <v-col cols="12">
                <v-textarea label=" " v-model="houseDetail[0].detailContents" ref="detailContents"></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green lighten-1" text @click="updateProperty">
            
          </v-btn>
          <v-btn color="green lighten-1" text @click="dialog = false">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</form>
</template>

<script>
  import axios from 'axios';
  import Swal from 'sweetalert2';

  export default {
    data: () => ({
      dialog: false,
      houseDetail: [],
      tags: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
      ],
      selectedTags: [],
      parking: '',
      state: ''
    }),
    props: {
      productId: Number
    },
    created() {
      axios({
          url: "/zippy/property/houseDetail",
          methods: "GET",
          params: {
            productId: this.productId
          }
        }).then(response => {
          //  
          this.houseDetail = response.data;
          this.state = this.houseDetail[0].productState == 1 ? '' : ' ';
          this.parking = this.houseDetail[0].parking == 1 ? '' : '';
          let temp = this.houseDetail[0].tags.split('/');
          for (let i = 0; i < temp.length; i++) {
            this.selectedTags.push(parseInt(temp[i]));
          }

        })
        .catch(error => {
          //   
          console.log('houseDetail fail!');
          console.log(error);
        })
    },
    methods: {
      updateProperty() {
        let result = 0;
        console.log(this.$refs.mainImg.value.name);

        axios({
            url: "/zippy/property/updateHouseProduct",
            methods: "PUT",
            params: {
              productState: this.state == '' ? 1 : 0,
              mainImg: this.$refs.mainImg.value.name,
              houseName: this.$refs.houseName.value,
              saleType: this.$refs.saleType.value,
              price: this.$refs.price.value,
              sigungu: this.$refs.sigungu.value,
              houseType: this.$refs.houseType.value,
              floor: this.$refs.floor.value,
              areaExclusive: this.$refs.areaExclusive.value,
              roomCnt: this.$refs.roomCnt.value,
              detailContents: this.$refs.detailContents.value,
              productId: this.houseDetail[0].productId
            }
          }).then(response => {
            //  
            console.log('updateHouseProduct success!');
            result++;
            if (result == 2) {
              Swal.fire({
                icon: 'success',
                title: '.',
                showConfirmButton: false,
                timer: 1500
              })
            }
          })
          .catch(error => {
            //   
            console.log('updateHouseProduct fail!');
            console.log(error);
          })

        let tags = '';

        this.$refs.tags.value.forEach(element => {
          tags += element + '/';
        });

        axios({
            url: "/zippy/property/updateHouseDetail",
            methods: "PUT",
            params: {
              streetAddress: this.$refs.streetAddress.value,
              houseFace: this.$refs.houseFace.value,
              parking: this.parking == '' ? 1 : 0, //this.$refs.parking.value == '' ? 1 : 0,
              bathCnt: this.$refs.bathCnt.value,
              constructionYear: this.$refs.constructionYear.value,
              tags: tags,
              productId: this.houseDetail[0].productId
            }
          }).then(response => {
            //  
            result++;
            if (result == 2) {
              Swal.fire({
                icon: 'success',
                title: '.',
                showConfirmButton: false,
                timer: 1500
              })
            }
          })
          .catch(error => {
            //   
            console.log(error);
          })

      }
    }
  }
</script>