    <div id="map" class="map" style="width:100%;height:350px;position:relative;overflow:hidden;"></div>
    <br><br>
    <ul id="category">
      <li id="SW8" data-order="                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                 