            mdi-subway-variant
          </v-icon>
        </v-btn>
        <p class="category_name"></p>
      </li>
      <li id="CS2" data-order="5">
          <v-btn class="mx-2" fab dark color="yellow darken-2">
          <v-icon dark>
            mdi-store
          </v-icon>
        </v-btn>
        <p class="category_name"></p>
      </li>
      <li id="CE7" data-order="4">
         <v-btn class="mx-2" fab dark color="orange darken-2">
          <v-icon dark>
            mdi-coffee
          </v-icon>
        </v-btn>
        <p class="category_name"></p>
      </li>
      <li id="BK9" data-order="0">
