      </li>
    </ul>
  </div>
</template>


<script>
  export default {
    props: ['address', 'name'],
    mounted() {
      window.kakao && window.kakao.maps ?
        this.initMap() :
        this.addKakaoMapScript();
    },
    methods: {
      addKakaoMapScript() {
        const script = document.createElement("script");
        /* global kakao */
        script.onload = () => kakao.maps.load(this.initMap);
      },
      initMap() {
        //        
        var placeOverlay = new kakao.maps.CustomOverlay({
            zIndex: 1
          }),
          contentNode = document.createElement('div'), //      
          markers = [], //   
          currCategory = ''; //      

        var mapContainer = document.getElementById('map'), //   div 
          mapOption = {
            center: new kakao.maps.LatLng(33.450701, 126.570667), //  
            level: 3 //   
          };


        //      
        var map = new kakao.maps.Map(mapContainer, mapOption);
6
        // -   
        var geocoder = new kakao.maps.services.Geocoder();

        //   
        geocoder.addressSearch(this.address, function (result, status) {

          //    
          if (status === kakao.maps.services.Status.OK) {

            var coords = new kakao.maps.LatLng(result[0].y, result[0].x);

            //     
            var marker = new kakao.maps.Marker({
              map: map,
              position: coords
            });

            //      
            map.setCenter(coords);
          }
        });

        /*    */
        //    
        var ps = new kakao.maps.services.Places(map);

        //  idle  
        kakao.maps.event.addListener(map, 'idle', searchPlaces);

        //     css class  
        contentNode.className = 'placeinfo_wrap';

        //     mousedown, touchstart  
        //        kakao.maps.event.preventMap   
        addEventHandle(contentNode, 'mousedown', kakao.maps.event.preventMap);
        addEventHandle(contentNode, 'touchstart', kakao.maps.event.preventMap);

        //    
        placeOverlay.setContent(contentNode);

        //     
        addCategoryClickEvent();

        //     
        function addEventHandle(target, type, callback) {
          if (target.addEventListener) {
            target.addEventListener(type, callback);
          } else {
            target.attachEvent('on' + type, callback);
          }
        }

        //    
        function searchPlaces() {
          if (!currCategory) {
            return;
          }

          //    
          placeOverlay.setMap(null);

          //     
          removeMarker();

          ps.categorySearch(currCategory, placesSearchCB, {
            useMapBounds: true
          });
        }

        //      
        function placesSearchCB(data, status, pagination) {
          if (status === kakao.maps.services.Status.OK) {

            //      
            displayPlaces(data);
          } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
            //        

          } else if (status === kakao.maps.services.Status.ERROR) {
            //            

          }
        }

        //    
        function displayPlaces(places) {

          //     
          //       
          var order = document.getElementById(currCategory).getAttribute('data-order');

          for (var i = 0; i < places.length; i++) {

            //    
            var marker = addMarker(new kakao.maps.LatLng(places[i].y, places[i].x), order);

            //      
            //     
            (function (marker, place) {
              kakao.maps.event.addListener(marker, 'click', function () {
                displayPlaceInfo(place);
              });
            })(marker, places[i]);
          }
        }

        //       
        function addMarker(position, order) {
          var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/2018/mw/m640/ico_marker.png',
            //'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png', //   url,   
            imageSize = new kakao.maps.Size(27, 28), //   
            imgOptions = {
              // spriteSize: new kakao.maps.Size(72, 208), //   
              // spriteOrigin: new kakao.maps.Point(46, (order * 36)), //       
              offset: new kakao.maps.Point(11, 28) //      
            },
            markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
            marker = new kakao.maps.Marker({
              position: position, //  
              image: markerImage
            });

          marker.setMap(map); //    
          markers.push(marker); //    

          return marker;
        }

        //       
        function removeMarker() {
          for (var i = 0; i < markers.length; i++) {
            markers[i].setMap(null);
          }
          markers = [];
        }

        //         
        function displayPlaceInfo(place) {
          var content = '<div class="placeinfo" style="background-color: white; text-align: center; padding: 15px; border-radius: 30px 10px;">' +
            '   <a style="text-decoration: none; color: inherit; font-weight: bold; font-size:medium; color: #E53935" href="' + place.place_url + '" target="_blank" title="' + place.place_name + '">' +
            place.place_name + '</a>';

          if (place.road_address_name) {
            content += '    <br><span title="' + place.road_address_name + '">' + place.road_address_name + '</span>' +
              '  <span class="jibun" title="' + place.address_name + '"><br>( : ' + place.address_name + ')</span>';
          } else {
            content += '    <br><span title="' + place.address_name + '">' + place.address_name + '</span>';
          }

          content += '    <br><span class="tel">' + place.phone + '</span>' +
            '</div>' +
            '<div class="after"></div>';

          contentNode.innerHTML = content;
          placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
          placeOverlay.setMap(map);
        }


        //     
        function addCategoryClickEvent() {
          var category = document.getElementById('category'),
            children = category.children;

          for (var i = 0; i < children.length; i++) {
            children[i].onclick = onClickCategory;
          }
        }

        //     
        function onClickCategory() {
          var id = this.id,
            className = this.className;

          placeOverlay.setMap(null);

          if (className === 'on') {
            currCategory = '';
            changeCategoryClass();
            removeMarker();
          } else {
            currCategory = id;
            changeCategoryClass(this);
            searchPlaces();
          }
        }

        //      
        function changeCategoryClass(el) {
          var category = document.getElementById('category'),
            children = category.children,
            i;

          for (i = 0; i < children.length; i++) {
            children[i].className = '';
          }

          if (el) {
            el.className = 'on';
          }
        }
      },

    }
  };
</script>

