    </div>
  </div>


  <!--  -->
  <div class="moveType-wrap">
    <h3>  .</h3>
    <div class="contact-btn1">
      <input type="button" class="custom-btn btn-1" @click="contact()" value="">
    </div>

    <div class="untact-btn2">
      <input type="button" class="custom-btn btn-2" @click="untact()" value="">
        
    </div>
    <br />
    <div class="contact-explain1">
      <table>
        <thead style="font-weight: bold;"> </thead>
        <tbody>
        <tr>
          <td>   </td>
