<template>
  <v-row>
    <v-dialog v-model="dialog2" persistent max-width="600px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn x-small elevation="2" fab dark v-bind="attrs" v-on="on">
          <v-icon small>mdi-plus</v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5"> </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6">
                <v-file-input label=' ' v-model="mainImg"></v-file-input>
              </v-col>
              <v-col cols="12" sm="6">
                <v-file-input label=' ' multiple small-chips truncate-length="15"></v-file-input>
              </v-col>
              <v-col cols="12">
                <v-text-field label="" v-model="houseName"></v-text-field>
              </v-col>
              <!--  ,  -->
              <v-col cols="12" sm="6">
                <v-select :items="['', '', '']" label=" " required v-model="saleType"></v-select>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="()" hint="  / ." required v-model="price"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="" hint="  " required v-model="sigungu"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label=" " hint=" 403" required v-model="streetAddress"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-select :items="['', '', '']" label="" required v-model="houseType"></v-select>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="" required v-model="floor"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label=" m" required v-model="areaExclusive"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-select :items="['', '', '', '']" label="" required v-model="houseFace"></v-select>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="" required v-model="constructionYear"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-select :items="['', '']" label=" " required v-model="parking"></v-select>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label=" " v-model="roomCnt"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label=" " required v-model="bathCnt"></v-text-field>
              </v-col>

              <v-col cols="12">
                <v-chip-group multiple active-class="green--text" v-model="selectedTags">
                  <v-chip v-for="tag in tags" :key="tag" filter outlined>
                    {{ tag }}
                  </v-chip>
                </v-chip-group>
              </v-col>

              <v-col cols="12">
                <v-textarea label=" " v-model="detailContents"></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="click">
            
          </v-btn>
          <v-btn color="blue darken-1" text @click="dialog2 = false">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  import axios from 'axios';
  import Swal from 'sweetalert2';

  export default {
    props: {
      email: String
    },
    data() {
      return {
        dialog2: false,
        mainImg: '',
        houseName: '',
        saleType: '',
        price: '',
        sigungu: '',
        houseType: '',
        floor: '',
        areaExclusive: '',
        roomCnt: '',
        detailContents: '',
        streetAddress: '',
        houseFace: '',
        parking: '',
        bathCnt: '',
        constructionYear: '',
        tags: [
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
        ],
        selectedTags: [],
      }
    },
    methods: {
      click() {
        console.log('', this.selectedTags);
        let tags = '';
        Array.from(this.selectedTags).forEach(element => {
          tags += element + '/';
        });

        this.insert(tags);
      },
      insert(tags) {
        let result = 0;

        axios({
            url: "http://localhost:8090/zippy/property/insertHouseProduct",
            methods: "POST",
            params: {
              mainImg: this.mainImg.name,
              houseName: this.houseName,
              saleType: this.saleType,
              price: this.price,
              sigungu: this.sigungu,
              houseType: this.houseType,
              floor: this.floor,
              areaExclusive: this.areaExclusive,
              roomCnt: this.roomCnt,
              detailContents: this.detailContents,
              email: this.email,
            }
          }).then(response => {
            //  
            result++;
            if (result == 2) {
              Swal.fire({
                icon: 'success',
                title: '.',
                showConfirmButton: false,
                timer: 1500
              })

            }
          })
          .catch(error => {
            //   
            console.log(error);
          })

        axios({
            url: "http://localhost:8090/zippy/property/insertHouseDetail",
            method: "POST",
            params: {
              streetAddress: this.streetAddress,
              houseFace: this.houseFace,
              parking: this.parking == '' ? 1 : 0,
              bathCnt: this.bathCnt,
              constructionYear: this.constructionYear,
              tags: tags
            }
          }).then(response => {
            //  
            result++;
            if (result == 2) {
              Swal.fire({
                icon: 'success',
                title: '.',
                showConfirmButton: false,
                timer: 1500
              })
            }
          })
          .catch(error => {
            //   
            console.log(error);
          })

      }
    }
  }
</script>