<template>
  <form id="shopInsert">
    <div>
      <div class="mx-auto pt-5" style="width:485px">
        <h3 style="margin:30px"></h3>
        <!--   -->
        <hr>
        <table id="insertTable" width="100%">
          <tbody>
            <tr>
              <td style="font-weight:bold"><span>*</span></td>
              <td>
                <b-form-input v-model="pro.proName" name="proName" placeholder=" "></b-form-input>
              </td>
            </tr>
            <tr>
              <td style="font-weight:bold"><span>*</span></td>
              <td>
                <b-form-input v-model="pro.proPrice" name="proPrice" placeholder=" "></b-form-input>
              </td>
            </tr>
            <tr>
              <td style="font-weight:bold"><span>*</span></td>
              <td>
                <b-form-input v-model="pro.deliveryCost" name="deliveryCost" placeholder=" "></b-form-input>
              </td>
            </tr>
            <tr>
              <td style="font-weight:bold"><span>*</span></td>
              <td>
                <v-select name="category"  v-model="pro.category" :items="cates" item-text="cates" item-value="cates" label="" return-object
                  dense outlined width="350" height="30"></v-select>
              </td>
            </tr>
            <td style="font-weight:bold; position:relative">
              <div class="mt-3" style="position:absolute; top:0"></div>
            </td>
            <td>
              <v-textarea width="350" solo name="proInfo" label=" "></v-textarea>
            </td>
            <tr>
              <td style="font-weight:bold"><span>*</span></td>
              <td style="padding-top:0; padding-bottom:0">
                <v-file-input name="image" accept="image/*" label="" v-model="pro.proMainImg" style="width:350px">
                </v-file-input>
              </td>
            </tr>
            <tr>
              <td style="font-weight:bold"></td>
              <td style="padding-top:0; padding-bottom:0">
                <v-file-input name="images" multiple label="" style="width:350px"></v-file-input>
              </td>
            </tr>
            <tr>
              <td style="font-weight:bold"></td>
              <!--    -->
              <div style="width:68px" class="mt-2 ml-auto pr-4">
                <!--  -->
                <div class="mr-2" style="display:inline-block">
                  <v-btn fab depressed dark color="#D6D6D6" @click="addOpt">
                    <v-icon dark>
                      mdi-plus
                    </v-icon>
                  </v-btn>
                </div>
                <!--  -->
                <!--  -->
                <div style="display:inline-block">
                  <v-btn fab depressed dark small color="#D6D6D6" @click="delOpt">
                    <v-icon dark>
                      mdi-minus
                    </v-icon>
                  </v-btn>
                </div>
                <!--  -->
              </div>
              <!--    -->
            </tr>
          </tbody>
        </table>
        <div v-for="(item, index) in option" :key="index" style="width:485px">
          <!--  -->
          <div class="pl-3 mt-5" style="width:100px; display:inline-block"></div>
          <div style="padding:10px; width:354px; display:inline-block">
            <input data-v-656fe1d6 type="text" class="form-control" v-model="item.optName">
          </div>
          <!--  -->
          <div class="pl-3 mb-5" style="width:100px;  display:inline-block"></div>
          <div style="padding:10px; width:354px;  display:inline-block">
            <input data-v-656fe1d6 type="text" class="form-control" v-model="item.optPrice">
          </div>
        </div>
        <!--   -->
        <input data-v-656fe1d6 hidden type="text" class="form-control"  name="email" v-model="email">
        <hr>
        <div>
          <div style="width:150px; margin-top:50px; margin-bottom:120px" class="mx-auto">
            <v-btn outlined color="#64c481" class="mr-3" @click="insertPro">
              
            </v-btn>
            <v-btn depressed color=#B3E3C3>
              
            </v-btn>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
  import axios from 'axios';
  import swal from 'sweetalert2';

  export default {
    data() {
      return {
        email: '',
        // 
        product: {},
        //null
        pro: [],
        //
        images: [],
        // 
        cates: ['', '/', '', '/', '', '', '', '', ''],
        //
        option: []
      }
    },
    created() {
      this.email = this.$store.state.loginInfo.email;
    },
    // 
    methods: {
      addOpt() {
        this.option.push({
          optName: '',
          optPrice: ''
        })
      },
      // 
      delOpt() {
        this.option.splice(this.option.length - 1, 1);
      },
      insertPro() {
        //null 
        if(!this.pro.proName || !this.pro.proPrice || !this.pro.deliveryCost || !this.pro.category || !this.pro.proMainImg){
          swal.fire({
            position: 'top-end',
            icon: 'warning',
            title: '   .',
            showConfirmButton: true
          })
          return;
        }

        //formData 
        var formData = new FormData(document.querySelector('#shopInsert'));
        formData.append('option', ""+JSON.stringify(this.option));

        console.log(formData);
        axios({
          url: "/zippy/shop/insertPro",
          method: "POST",
          data: formData
        }).then(res => {
          console.log(res);
          swal.fire({
            icon: 'success',
            title: ' .',
            showConfirmButton: true
          })
        }).catch(error => {
          console.log(error);
        })
      }
    }
  }
</script>

<style scoped>
  input {
    width: 350px !important;
    height: 30px !important;
  }

  td {
    padding: 10px;
  }
  .v-btn {
    width: 20px;
    height: 20px;
  }

  span {
    color: #64c481;
  }
</style>