<template>
  <div class="mx-auto pt-5" id="cart-con">
    <h3 style="margin:30px"></h3>
    <v-card>
      <v-card-title>
        <v-text-field @keyup="enterkey()" id="search" append-icon="mdi-magnify" label="/" single-line
          hide-details>
        </v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="products">
        <!--  -->
        <template v-slot:item.proMainImg="{ item }">
          <img class="ma-5" :src="'/zippy/common/img/shop/'+item.proMainImg" width="150" height="150">
        </template>
        <!--  -->
        <template v-slot:item.update="{ item }">
          <v-btn v-if="item.proStatus==0" depressed color=#B3E3C3 class="mr-2"
            @click="updateStatus(item.proNo, item.proStatus)">
            
          </v-btn>
          <v-btn v-if="item.proStatus==1" depressed color=#D6D6D6 class="mr-2"
            @click="updateStatus(item.proNo, item.proStatus)">
            
          </v-btn>
          <v-btn depressed outlined color=#64c481 @click="updateProInfo(item.proNo)">
            
          </v-btn>
        </template>
      </v-data-table>
    </v-card>

  </div>
</template>

<script>
  import axios from 'axios';
  import swal from 'sweetalert2';

  export default {
    data() {
      return {
        singleSelect: false,
        selected: [],
        headers: [{
            text: '',
            align: '',
            sortable: false,
            value: 'proNo',
          },
          {
            text: '',
            value: 'proMainImg'
          },
          {
            text: '',
            value: 'proName'
          },
          {
            text: '',
            value: 'category'
          },
          {
            text: '',
            value: 'update'

          }
        ],
        products: []
      }
    },
    methods: {
      // 0->1 
      updateStatus(no, st) {
        let outside = this;
        if (st == 0) {
          swal.fire({
            icon: 'success',
            title: '  ?',
            showConfirmButton: true,
            showCancelButton: true
          }).then((result) => {
            if (result.isConfirmed) {
              //st 0->1 
              st = 1;
              //db update
              outside.updateProStatus(no, st);
              //update  
              outside.changeStatus(no, st);
            } else if (result.isDenied) {
              return;
            }
          });
        } else {
          swal.fire({
            icon: 'success',
            title: '   ?',
            showConfirmButton: true,
            showCancelButton: true
          }).then((result) => {
            if (result.isConfirmed) {
              //st 1->0 
              st = 0;
              //db update
              outside.updateProStatus(no, st);
              //update  
              outside.changeStatus(no, st);
            } else if (result.isDenied) {
              return;
            }
          });
        }
      },
      updateProStatus(no, st) {
        // update
        axios({
          url: "/zippy/shop/updateStatus",
          method: "POST",
          data: {
            proNo: no,
            proStatus: st
          },
          method: "POST",
        }).then(res => {
          console.log(res);
        }).catch(error => {
          console.log(error);
        })
      },
      //proStatus  
      changeStatus(no, st) {
        for (var i in this.products) {
          if (this.products[i].proNo == no)
            if (st == 0) {
              this.products[i].proStatus = 0;
            } else {
              this.products[i].proStatus = 1;
            }
        }
      },
      //  
      updateProInfo(no) {
        this.$router.push({
          name: 'shopUpdatePro',
          query: {
            proNo: no
          }
        })
      },
      /// ()
      enterkey() {
        var searchValue = document.querySelector("#search").value;
        if (window.event.keyCode == 13) {
          //  
          axios({
            url: "/zippy/shop/myProList",
            method: "POST",
            data: {
              email: this.$store.state.loginInfo.email
            },
            params: {
              keyword: searchValue
            },
            method: "POST",
          }).then(res => {
            console.log(res);
            this.products = res.data;
            console.log(this.products);
          }).catch(error => {
            console.log(error);
          })
        }
      }
    },
    created() {
      //  
      axios({
        url: "/zippy/shop/myProList",
        method: "POST",
        data: {
          email: this.$store.state.loginInfo.email
        },
        method: "POST",
      }).then(res => {
        console.log(res);
        this.products = res.data;
        console.log(this.products);
      }).catch(error => {
        console.log(error);
      })
    }
  };
</script>

<style scoped>
  #cart-con {
    width: 70%;
  }

  .v-toolbar__title {
    font-weight: bold;
  }

  .v-application a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.87) !important;
  }

  .v-application a:hover {
    text-decoration: underline;
  }

  .v-btn {
    font-weight: bold;
  }

  .v-image :hover {
    cursor: pointer;
  }
</style>