<template>
  <div>
    <move-nav-bar @click="categoryVal=$event.target.innerText"></move-nav-bar>
  
    <div class="result-wrap">
    <form>
      <div class="move-main-title">
        <h3>  </h3>
      </div>
    
      <div id="used-main-dropbox">
