      <h2 style="font-weight:bold">   .</h2>
    </div>


    <div class="panel">
      <v-expansion-panels>
        <v-expansion-panel
        v-if="list.length != 0" v-for="item in list"
        >
          <v-expansion-panel-header>
            <span>NO.{{item.estimateNo}}</span>  &nbsp;&nbsp;&nbsp;  : <span>{{item.requestDate}}</span>&nbsp;&nbsp;&nbsp;   : <span>{{item.estimateType}}</span>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <div class="info">
            <div >  : <span>{{item.moveType}}</span></div>
            <div > : <span>{{item.movingDate}}</span></div>
            <div > : <span>{{item.movingTime}}</span></div>
            <div >  : <span>(){{item.departZipCode}}</span>    <span>{{item.departAddress}}</span> <span>{{item.departDetail}}</span></div>
            <div >  : <span>(){{item.arriveZipCode}}</span>    <span>{{item.arriveAddress}}</span> <span>{{item.arriveDetail}}</span></div>
            <div>   : <div class="move-info"><span v-html="par(item.commonOption)"></span></div></div>
            <div v-if="item.estimateType == ''">
            <div >  : <div class="move-info"><span class="move-info" v-html="my(item.movingOption)"></span></div></div>
            </div>  
     
            <div v-if="item.movingMemo != null">
            <div >  : <span>{{item.movingMemo}}</span></div>
            </div>
            <div v-if="item.estimateType == ''">
