            <div >  : <span>{{item.visitTime}}</span></div>
            </div>
            <div v-if="item.estimateType == ''" id="imgPanel">
            </div> 
             <!--   -->

              <v-card-actions>

            <v-spacer></v-spacer>
                <v-label ></v-label> <v-btn
