              </div>

              
              <div v-for="img in photoList">
                <img v-if="img.estimateNo == item.estimateNo && img.imgType ==3" 
                :src="'/zippy/common/img/move/' + img.houseImg"/>
              </div>
            </div>
            </div>
          </v-expand-transition>
