<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent max-width="290">
      <template v-slot:activator="{ on, attrs }">
        <div id="reportButton" v-bind="attrs" v-on="on">
          <img
            src="data:image/png;base64,"
            width="15" height="15" alt=" " />
        </div>
      </template>
      <v-card>
        <v-card-title class="text-h7">
          <b></b>
        </v-card-title>
        <v-card-text>    .  .</v-card-text>
        <v-card-text>
          <v-radio-group v-model="reportContents">
            <v-radio label=" " value=" " color="green lighten-3"></v-radio>
            <v-radio label="  " value="  " color="green lighten-3"></v-radio>
          </v-radio-group>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green accent-3" text @click="dialog = false">
            
          </v-btn>
          <v-btn color="green accent-3" text @click="report">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  import Swal from 'sweetalert2';
  import axios from 'axios';


  export default {
    props: ['productId'],
    data() {
      return {
        dialog: false,
        reportContents: ' '
      }
    },
    methods: {
      report() {
        if (this.$store.state.loginInfo) {
          axios({
              url: "http://localhost:8090/zippy/common/addReport",
              method: "POST",
              params: {
                email: this.$store.state.loginInfo.email,
                reportType: '',
                reportContents: this.reportContents,
                serviceType: 0,
                serviceId: this.productId
              },
            }).then(response => {
              //  
              console.log('addReport success!');

              Swal.fire({
                icon: 'success',
                title: ' .',
                showConfirmButton: false,
                timer: 1500
              })
            })
            .catch(error => {
              //   
              console.log('addReport fail!');
              console.log(error);
            })
        } else {
          Swal.fire({
            icon: 'warning',
            title: '  .',
            showConfirmButton: false,
            timer: 1500
          })
        }
        this.dialog = false;
      }
    }
  }
</script>

<style scoped>
  #reportButton {
    text-align: right;
    margin-top: 5px;
  }
</style>